import { useEffect, useContext } from "react";
import { AppContext } from "../../Contexts/AppContext";
import "./splashscreen.scss";
import SIGNALS from "../../signals";
import SCREENS from "../../screens";
const ue4 = window.ue4;

export default function SplashScreen() {
  const { navigate } = useContext(AppContext);
  const clickSplashScreen = () => {
    navigate(SCREENS.SPLASHSCREEN, SCREENS.DESKTOP);
  };
  useEffect(() => ue4(SIGNALS.SOUND__SPLASHSCREEN), []);
  return (
    <div
      onClick={clickSplashScreen}
      className="splashscreen-wrapper background"
    >
      <h1>KEYBALL</h1>
      <div className="splashscreen-waves-wrapper">
        <svg
          className="waves"
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 24 150 28"
          preserveAspectRatio="none"
          shapeRendering="auto"
        >
          <defs>
            <path
              id="gentle-wave"
              d="M-160 44c30 0 58-18 88-18s 58 18 88 18 58-18 88-18 58 18 88 18 v44h-352z"
            />
          </defs>
          <g className="parallax">
            <use
              xlinkHref="#gentle-wave"
              x="48"
              y="0"
              fill="rgba(255,255,255,0.7"
            />
            <use
              xlinkHref="#gentle-wave"
              x="48"
              y="3"
              fill="rgba(255,255,255,0.5)"
            />
            <use
              xlinkHref="#gentle-wave"
              x="48"
              y="5"
              fill="rgba(255,255,255,0.3)"
            />
            <use xlinkHref="#gentle-wave" x="48" y="7" fill="#fff" />
          </g>
        </svg>
      </div>
    </div>
  );
}
