import SIGNALS from "../../signals";
const ue4 = window.ue4;

export default function Quit() {
  return (
    <div>
      <h1>Are you sure you want to leave this match?</h1>
      <button onClick={() => ue4(SIGNALS.INGAME_LEAVE_MATCH, {})}>Yes</button>
      <button onClick={() => ue4(SIGNALS.INGAME_CLOSE_MENU, {})}>Cancel</button>
    </div>
  );
}
