import "./keyboard-detector.css";
import { useEffect, useState } from "react";

const slots = [
  { localGlyph: null, scancode: "Digit1", active: false },
  { localGlyph: null, scancode: "Digit2", active: false },
  { localGlyph: null, scancode: "Digit3", active: false },
  { localGlyph: null, scancode: "Digit4", active: false },
  { localGlyph: null, scancode: "Digit5", active: false },
  { localGlyph: null, scancode: "Digit6", active: false },
  { localGlyph: null, scancode: "Digit7", active: false },
  { localGlyph: null, scancode: "Digit8", active: false },
  { localGlyph: null, scancode: "Digit9", active: false },
  { localGlyph: null, scancode: "Digit0", active: false },
  { localGlyph: null, scancode: "KeyQ", active: false },
  { localGlyph: null, scancode: "KeyW", active: false },
  { localGlyph: null, scancode: "KeyE", active: false },
  { localGlyph: null, scancode: "KeyR", active: false },
  { localGlyph: null, scancode: "KeyT", active: false },
  { localGlyph: null, scancode: "KeyY", active: false },
  { localGlyph: null, scancode: "KeyU", active: false },
  { localGlyph: null, scancode: "KeyI", active: false },
  { localGlyph: null, scancode: "KeyO", active: false },
  { localGlyph: null, scancode: "KeyP", active: false },
  { localGlyph: null, scancode: "KeyA", active: false },
  { localGlyph: null, scancode: "KeyS", active: false },
  { localGlyph: null, scancode: "KeyD", active: false },
  { localGlyph: null, scancode: "KeyF", active: false },
  { localGlyph: null, scancode: "KeyG", active: false },
  { localGlyph: null, scancode: "KeyH", active: false },
  { localGlyph: null, scancode: "KeyJ", active: false },
  { localGlyph: null, scancode: "KeyK", active: false },
  { localGlyph: null, scancode: "KeyL", active: false },
  { localGlyph: null, scancode: "Semicolon", active: false },
  { localGlyph: null, scancode: "KeyZ", active: false },
  { localGlyph: null, scancode: "KeyX", active: false },
  { localGlyph: null, scancode: "KeyC", active: false },
  { localGlyph: null, scancode: "KeyV", active: false },
  { localGlyph: null, scancode: "KeyB", active: false },
  { localGlyph: null, scancode: "KeyN", active: false },
  { localGlyph: null, scancode: "KeyM", active: false },
  { localGlyph: null, scancode: "Comma", active: false },
  { localGlyph: null, scancode: "Period", active: false },
  { localGlyph: null, scancode: "Slash", active: false },
];
export default function App() {
  const [userInputs, setUserInputs] = useState(slots);

  useEffect(() => {
    document.addEventListener("keydown", (event) => {
      setUserInputs((prev) =>
        prev.map((key) =>
          key.scancode === event.code
            ? { ...key, localGlyph: event.key, active: true }
            : key
        )
      );
    });
  }, []);
  return (
    <div className="keyboard-detector">
      <h1>Please start typing...</h1>
      <div className="keyboard">
        {userInputs.map((s, i) => (
          <div key={s.scancode} className={s.active ? "active" : "inactive"}>
            {s.active ? s.localGlyph : ""}
          </div>
        ))}
      </div>
    </div>
  );
}
