import SIGNALS from "../../signals";
import SCREENS from "../../screens";
import { useContext, useEffect } from "react";
import BackIcon from "../icons/Back";
import { AppContext } from "../../Contexts/AppContext";

import "./FileExplorer.scss";
const ue4 = window.ue4;

export default function FileExplorer() {
  const { gameMods, navigate, settings } = useContext(AppContext);

  const handleFileSelection = (mod) => {
    ue4(SIGNALS.SETTINGS__SELECT_ACTIVE_GAME_MOD, {
      ...settings,
      activeGameMod: mod.id,
    });
    navigate(SCREENS.FILE_EXPLORER, SCREENS.MOD_EDITOR);
  };

  return (
    <div className="file-explorer">
      <nav className="header">
        <div className="header-title">
          <h1>Files</h1>
        </div>
        <div
          onClick={() => navigate(SCREENS.FILE_EXPLORER, SCREENS.MOD_EDITOR)}
          className="newspad fs"
        >
          <BackIcon />
        </div>
      </nav>
      <div className="file-explorer-content content">
        <div className="file-explorer-files">
          {gameMods.map((mod) => (
            <div className="file">
              <div className="file-name">{mod.meta.name}</div>
              <div className="file-actions">
                <div
                  className="file file-select"
                  onClick={() =>
                    handleFileSelection({
                      id: mod.meta.id,
                      mod_type: mod.MOD_TYPE,
                    })
                  }
                >
                  select
                </div>
                <div
                  className="file file-edit"
                  onClick={() =>
                    handleFileSelection({
                      id: mod.meta.id,
                      mod_type: mod.MOD_TYPE,
                    })
                  }
                >
                  edit
                </div>
                <div
                  className="file file-edit"
                  onClick={() =>
                    handleFileSelection({
                      id: mod.meta.id,
                      mod_type: mod.MOD_TYPE,
                    })
                  }
                >
                  delete
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
      <div className="file-explorer-start-row">
        <button className="warning big" onClick={console.log}>
          import a mod from JSON
        </button>
        <button className="success big" onClick={console.log}>
          look online at mod store
        </button>
      </div>
    </div>
  );
}
