import React, { useContext } from "react";
import "./formEditor.css";
import GAME_MOD from "../../mod-schemas/GAME_MOD";
import { AppContext } from "../../Contexts/AppContext";

const icons = {
  meta: "meta",
  board: "board",
  scoring: "scoring",
  "enabled moves": " moves",
  keycaps: "keycaps",
  goals: "goals",
  reset: "reset",
  spatial: "spatial",
};

export default function FormEditor({
  updateMod,
  lastSelectedFileInUIType,
  activeMod,
}) {
  const { fullscreen } = useContext(AppContext);
  const processChange = (e) => {
    const { name, value } = e.target;
    let finalValue = value;

    if (e.target.type === "checkbox") {
      finalValue = e.target.checked;
    }

    if (["number", "range"].includes(e.target.type)) {
      finalValue = parseInt(value);
    }

    const subCategory = e.target.dataset.subcategory;
    const fieldType = e.target.dataset.fieldtype;
    updateMod({
      modType: lastSelectedFileInUIType,
      mod: activeMod.meta.id,
      field: name,
      newValue: finalValue,
      subCategory,
      fieldType,
    });
  };

  function showField(schemaField, activeField, key, level, subCategory) {
    return (
      <div
        className={`level-${level} mod-field ${
          schemaField[key].readOnly ? "read-only" : ""
        } ${schemaField[key].hidden ? "hidden" : ""}`}
      >
        <p className="mod-field-label">{key}</p>
        {
          {
            string: schemaField[key].readOnly ? (
              <h1 className="read-only-value">{activeField[key]}</h1>
            ) : (
              <input
                type="text"
                name={key}
                data-subcategory={subCategory}
                data-fieldtype={schemaField[key].type}
                value={activeField[key]}
                onChange={processChange}
                disabled={schemaField[key].readOnly}
              />
            ),
            enum: (
              <select
                name={key}
                data-subcategory={subCategory}
                data-fieldtype={schemaField[key].type}
                defaultValue={activeField[key]}
                onChange={processChange}
              >
                {schemaField[key].options?.map((o) => (
                  <option key={o} value={o}>
                    {o}
                  </option>
                ))}
              </select>
            ),
            integer: (
              <div>
                <input
                  type="number"
                  className="fat"
                  name={key}
                  data-subcategory={subCategory}
                  data-fieldtype={schemaField[key].type}
                  value={activeField[key]}
                  onChange={processChange}
                  min={schemaField[key].min}
                  max={schemaField[key].max}
                />

                <label>
                  <input
                    onChange={processChange}
                    type="range"
                    data-subcategory={subCategory}
                    data-fieldtype={schemaField[key].type}
                    name={key}
                    step={schemaField[key].max > 99 ? 10 : 1}
                    min={schemaField[key].min}
                    max={schemaField[key].max}
                    value={activeField[key]}
                    className="slider"
                  />
                </label>
              </div>
            ),
            boolean: (
              <div className="radio">
                <label className="switch">
                  <input
                    type="checkbox"
                    data-subcategory={subCategory}
                    data-fieldtype={schemaField[key].type}
                    data-prev={activeField[key]}
                    name={key}
                    checked={activeField[key]}
                    onChange={processChange}
                  />
                  <span
                    data-subcategory={subCategory}
                    data-fieldtype={schemaField[key].type}
                    className="switch-slider"
                  ></span>
                </label>
                <h1>
                  {activeField[key] ? (
                    <span style={{ color: "limegreen" }}>ON</span>
                  ) : (
                    "OFF"
                  )}
                </h1>
              </div>
            ),
          }[schemaField[key].type]
        }
      </div>
    );
  }

  function recursivelyShowForm(
    nextSchemaLevel,
    nextActiveLevel,
    key,
    level,
    subCategory
  ) {
    const keys = Object.keys(nextSchemaLevel);
    return (
      <>
        {keys.map((k) => {
          return (
            <div
              className="section-child"
              key={k}
              style={{
                width: fullscreen && nextSchemaLevel[k]?.type ? "50%" : null,
              }}
            >
              {!level && (
                <>
                  <div id={`${k}-jump`} className={`jump`}></div>
                  <h1>{k}</h1>
                </>
              )}
              <div id={k} className={!level ? "section" : undefined}>
                {nextSchemaLevel[k].type
                  ? showField(nextSchemaLevel, nextActiveLevel, k, level, key)
                  : recursivelyShowForm(
                      nextSchemaLevel[k],
                      nextActiveLevel[k],
                      k,
                      level + 1,
                      subCategory
                    )}
              </div>
            </div>
          );
        })}
      </>
    );
  }
  return (
    <div className="form-editor">
      <div className="form-editor-sidebar">
        {Object.keys(GAME_MOD).map((k) => {
          return (
            <a className="form-editor-sidebar-item" key={k} href={`#${k}-jump`}>
              <span>{icons[k]}</span>
            </a>
          );
        })}
      </div>

      <div className="form-editor-main">
        {activeMod && recursivelyShowForm(GAME_MOD, activeMod, "", 0, null)}
      </div>
    </div>
  );
}
