import SIGNALS from "../../signals";
import SCREENS from "../../screens";
import { useContext, useState, useEffect } from "react";
import BackIcon from "../icons/Back";
import { AppContext } from "../../Contexts/AppContext";

import "./lobby.scss";
const ue4 = window.ue4;

export default function Lobby() {
  // THIS COMPONENT NEEDS
  // server info (isOnlineGame, isHost, name, players, layouts)
  const { quitMultiplayer } = useContext(AppContext);
  const [connectedPlayers, setConnectedPlayers] = useState([]);
  const [gameMod, setGameMod] = useState("088010880");
  const [level, setLevel] = useState("Beach");

  const handleInvite = () => {
    ue4(SIGNALS.SESSION_MULTIPLAYER__INVITE_CLICK);
  };
  const handleStart = () => {
    ue4(SIGNALS.SESSION_LOCAL__START, { level, gameMod });
  };
  return (
    <div className="lobby">
      <nav className="header">
        <div className="header-title">
          <h1>lobby</h1>
        </div>
        <div onClick={quitMultiplayer} className="newspad fs">
          <BackIcon />
        </div>
      </nav>
      <div className="lobby-content content">
        <div>
          <div className="lobby-settings">
            <h1>settings</h1>
            <h2>game mod</h2>
            <select
              name="GAME_MOD"
              defaultValue="volleyball"
              onChange={(e) => setGameMod(e.target.value)}
            >
              {[{ id: "088010880", label: "volleyball" }].map((o) => (
                <option key={o.id} value={o.id}>
                  {o.label}
                </option>
              ))}
            </select>
            <p>
              game mod description: qwpdokqw pdokqwd pqwodkq wpdoqwk dpqwodk
              qwpdokqw dpoqwkd pqwodk qwpodk qwpdokq wpdokqw dpoqwkd p{" "}
            </p>
            <h2>MAP / world mod</h2>
            <select
              name="WORLD_MOD"
              defaultValue="court"
              onChange={(e) => setLevel(e.target.value)}
            >
              {["Beach"].map(
                (
                  o //, "court", "desk"
                ) => (
                  <option key={o} value={o}>
                    {o}
                  </option>
                )
              )}
            </select>
          </div>
          <div className="lobby-players">
            <h1>players</h1>
            {connectedPlayers.map((player) => (
              <div className="player">
                <div className="player-name">{player.name}</div>
                <div className="player-primary">{player.primary}</div>
                <div className="player-secondary">{player.secondary}</div>
              </div>
            ))}
          </div>
        </div>
        <div className="lobby-start-row">
          <button className="warning big" onClick={handleInvite}>
            invite a friend
          </button>
          <button className="success big" onClick={handleStart}>
            start
          </button>
        </div>
      </div>
    </div>
  );
}
