import SIGNALS from "../../signals";
import SCREENS from "../../screens";
import BackIcon from "../icons/Back";
import { GridLoader } from "react-spinners";
import { AppContext } from "../../Contexts/AppContext";
import { useContext, useState } from "react";
import "./ServerList.scss";
const ue4 = window.ue4;

export default () => {
  const { navigate, serverListState } = useContext(AppContext);
  return (
    <div className="serverlist-container">
      <nav className="header">
        <div className="header-title">
          <h1>Public Games</h1>
        </div>
        <div
          onClick={() => navigate(SCREENS.SERVER_LIST, SCREENS.DESKTOP)}
          className="newspad fs"
        >
          <BackIcon />
        </div>
      </nav>

      <div className="serverlist-content">
        <div className="serverlist-list">
          <div className="serverlist-item">
            <div className="serverlist-item-name">LOBBY</div>
            <div className="serverlist-item-game">game mod</div>
            <div className="serverlist-item-world">world mod</div>
            <div className="serverlist-item-players">players</div>
            <div className=""></div>
          </div>
          {serverListState.length ? (
            serverListState.map((o) => (
              <div key={o.name} className="serverlist-item">
                <div className="serverlist-item-name">{o.name}</div>
                <div className="serverlist-item-game">{o.game}</div>
                <div className="serverlist-item-map">{o.map}</div>
                <div className="serverlist-item-players">
                  {o.players}/{o.max}
                </div>
                <div className="serverlist-item-join">JOIN</div>
              </div>
            ))
          ) : (
            <div className="central">
              <GridLoader color="white" speedMultiplier="0.2" />
            </div>
          )}
        </div>
        <div className="row">
          <button
            className="warning big"
            onClick={() => ue4(SIGNALS.SESSION_MULTIPLAYER__HOST)}
          >
            host a game
          </button>
        </div>
      </div>
    </div>
  );
};
