import SCREENS from "../../screens";
import { AppContext } from "../../Contexts/AppContext";
import { useContext, useState, useEffect } from "react";
import BackIcon from "../icons/Back";

export default function DEBUG_MODS() {
  const [test, setTest] = useState("qwdqwdqwd");
  const { toggleFullscreen, navigate, gameMods, UIState, setUIState } =
    useContext(AppContext);
  useEffect(() => {
    window.setUIState = setUIState;
    window.setTest = setTest;
  }, []);
  const { location, lastSelectedFileInUI, lastSelectedFileInUIType } = UIState;
  return (
    <div id="mod-editor" style={{ color: "black" }}>
      <nav>
        <div id="mod-editor-tabs">
          <div
            onClick={() => navigate(SCREENS.MOD_EDITOR, SCREENS.DESKTOP)}
            className="navpad fs"
          >
            <BackIcon />
          </div>
        </div>
      </nav>
      <div className="content">
        <h1>{test}</h1>
        <h1>{UIState.location}</h1>

        {JSON.stringify({
          location,
          lastSelectedFileInUI,
          lastSelectedFileInUIType,
        })}
      </div>
      <div className="content">{JSON.stringify(gameMods)}</div>
    </div>
  );
}
