import { useEffect, useState, useContext } from "react";
import "./boot.scss";
import SCREENS from "../../screens";
import { AppContext } from "../../Contexts/AppContext";

const initialDelay = 3000;

export default function Boot() {
  const [timer, setTimer] = useState(0);
  const { navigate } = useContext(AppContext);

  useEffect(() => {
    let bootupInterval = setInterval(() => setTimer((p) => p + 200), 200);
    return () => clearInterval(bootupInterval);
  }, []);

  useEffect(() => {
    if (timer > 8500 + initialDelay) {
      navigate(SCREENS.BOOT, SCREENS.SPLASHSCREEN);
    }
  }, [timer]);

  return (
    <div
      className="boot"
      style={{ background: timer > 700 ? "rgb(50,50,50)" : "black" }}
    >
      {timer > initialDelay && (
        <div id="bootlogo-wrapper" className="flex">
          <img className="bootlogo" src="keebOS.png" alt="keebos logo" />
          <h1>KEEBos V0.01</h1>
        </div>
      )}

      {timer > 2000 + initialDelay && (
        <>
          <span className="bootline">Made By Christopher Shelley . . .</span>
          <span className="bootline">support.keyball@embassy.computer</span>
        </>
      )}
      {timer > 6000 + initialDelay && (
        <span className="bootline">steam connected . . .</span>
      )}
      {timer > 7100 + initialDelay && (
        <span className="bootline">daily stuff downloaded . . .</span>
      )}
      {timer > 7100 + initialDelay && (
        <span className="bootline">localisation check . . .</span>
      )}
      {timer > 7500 + initialDelay && (
        <span className="bootline">user preferences loaded . . .</span>
      )}
    </div>
  );
}
