import React, { createContext, useState, useEffect } from "react";
import SIGNALS from "../signals";
import SCREENS from "../screens";
import createMod from "../utilities/mod";
const ue4 = window.ue4;

export const AppContext = createContext();

window.updateStates = {
  setUIState: null,
  setGameMods: null,
  setMacroMods: null,
  setBoardMods: null,
  setWorldMods: null,
  setOSMods: null,
  setSettings: null,
  setUserSteamInfo: null,
  setFullscreen: null,
  setLobbyState: null,
};
function parseQuery(queryString) {
  var query = {};
  var pairs = (
    queryString[0] === "?" ? queryString.substr(1) : queryString
  ).split("&");
  for (var i = 0; i < pairs.length; i++) {
    var pair = pairs[i].split("=");
    query[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1] || "");
  }
  return query;
}
const initialURLParameters = parseQuery(window.location.hash.split("#?")[1]);

const AppContextProvider = ({ children }) => {
  const [UIState, setUIState] = useState({
    location: initialURLParameters.screen || SCREENS.BOOT,
    lastSelectedFileInUI: null,
    lastSelectedFileInUIType: null,
  });
  const [intitialStateReceived, setIntitialStateReceived] = useState(false);
  const [fullscreen, setFullscreen] = useState(
    initialURLParameters.popout === "true"
  );
  useEffect(() => {
    console.log(UIState);
  }, [UIState]);
  const [gameMods, setGameMods] = useState([]);
  const [macroMods, setMacroMods] = useState([]);
  const [boardMods, setBoardMods] = useState([]);
  const [worldMods, setWorldMods] = useState([]);

  const [updatedStateToSendToUE4, setUpdatedStateToSendToUE4] = useState(null);

  const [OSMods, setOSMods] = useState([]);
  const [settings, setSettings] = useState();
  const [userSteamInfo, setUserSteamInfo] = useState(null);
  const [lobbyState, setLobbyState] = useState(null);
  const [serverListState, setServerListState] = useState([
    // {
    //   name: "Server 1",
    //   game: "Game 1",
    //   map: "World 1",
    //   players: 10,
    //   max: 20,
    // },
  ]);

  // expose setters globally for UE4
  useEffect(() => {
    window.updateStates = {
      setUIState,
      setGameMods,
      setMacroMods,
      setBoardMods,
      setWorldMods,
      setOSMods,
      setSettings,
      setUserSteamInfo,
      setFullscreen,
      setLobbyState,
      setServerListState,
    };
    if (process.env.NODE_ENV === "development") {
      setGameMods([createMod("GAME_MOD")]);
      setSettings({
        activeBoardMod: "default",
        activeGameMod: "12312312",
        activeMacroMod: null,
        activeOSMod: "default",
        activeWorldMod: "default",
        activeKeyboardLayout: "qwerty",
        audio: {
          audioEnabled: true,
          audioVolume: 0.5,
          musicEnabled: true,
          musicVolume: 0.5,
          sfxEnabled: true,
          sfxVolume: 0.5,
        },
        chat: {
          chatEnabled: true,
          chatFontSize: 16,
        },
        customLayouts: [],
      });
    } else {
      ue4(SIGNALS.GENERAL__READY_FOR_STATE);
    }
    return () => {
      window.updateStates = {
        setUIState: null,
        setGameMods: null,
        setMacroMods: null,
        setBoardMods: null,
        setWorldMods: null,
        setOSMods: null,
        setSettings: null,
        setUserSteamInfo: null,
        setLobbyState: null,
        setServerListState: null,
      };
    };
  }, []);

  useEffect(() => {
    if (!intitialStateReceived) {
      gameMods.length && settings && setIntitialStateReceived(true);
    }
    console.log(gameMods, settings);
    if (settings) {
      setUIState((p) => ({
        ...p,
        lastSelectedFileInUI: settings.activeGameMod,
        lastSelectedFileInUIType: "GAME_MOD",
      }));
    }
  }, [settings, gameMods]);

  const updateMod = ({
    modType,
    mod,
    field,
    newValue,
    subCategory,
    fieldType,
  }) => {
    const updaters = {
      GAME_MOD: setGameMods,
      SETTINGS: setSettings,
      BOARD_MOD: setBoardMods,
      MACRO_MOD: setMacroMods,
      OS_MOD: setOSMods,
      WORLD_MOD: setWorldMods,
    };
    updaters[modType]((prev) => {
      const res = prev.map((thisMod) => {
        if (thisMod.meta.id === mod) {
          if (subCategory) {
            return {
              ...thisMod,
              [subCategory]: { ...thisMod[subCategory], [field]: newValue },
            };
          } else {
            return {
              ...thisMod,
              [field]: newValue,
            };
          }
        } else {
          return thisMod;
        }
      });
      setUpdatedStateToSendToUE4({
        field,
        modType,
        fieldType,
        newValue,
        data: res.find((thisMod) => thisMod.meta.id === mod),
      });
      return res;
    });
  };

  useEffect(() => {
    if (updatedStateToSendToUE4) {
      const { field, modType, fieldType, newValue } = updatedStateToSendToUE4;

      const modSignals = {
        GAME_MOD: "GAME_MOD__UPDATE",
        MACRO_MOD: "MACRO_MOD__UPDATE",
        BOARD_MOD: "BOARD_MOD__UPDATE",
        WORLD_MOD: "WORLD_MOD__UPDATE",
        OS_MOD: "OS_MOD__UPDATE",
        SETTINGS: "SETTINGS__LAYOUT_UPDATE",
        LOBBY_STATE: "LOBBY_STATE__UPDATE",
      };

      console.log(modSignals[modType], {
        changedField: field,
        changedFieldType: fieldType,
        newValue,
        changedMod: updatedStateToSendToUE4.data,
      });

      ue4(modSignals[modType], {
        changedField: field,
        changedFieldType: fieldType,
        newValue,
        changedMod: updatedStateToSendToUE4.data,
      });
    }
  }, [updatedStateToSendToUE4]);

  const navigate = (from, to) => {
    ue4(SIGNALS.SOUND__CLICK);
    if (process.env.NODE_ENV === "production") {
      ue4(SIGNALS.GENERAL__NAVIGATE, {
        oldScreen: from,
        newScreen: to,
      });
    } else {
      setUIState((p) => ({ ...p, location: to }));
    }
  };
  const quitMultiplayer = () => {
    ue4(SIGNALS.SOUND__CLICK);
    if (process.env.NODE_ENV === "production") {
      ue4(SIGNALS.SESSION_MULTIPLAYER__LEAVE);
    } else {
      setUIState((p) => ({ ...p, location: SCREENS.DESKTOP }));
    }
  };

  return (
    <AppContext.Provider
      value={{
        navigate,
        updateMod,
        fullscreen,
        UIState,
        gameMods,
        macroMods,
        boardMods,
        worldMods,
        OSMods,
        settings,
        userSteamInfo,
        intitialStateReceived,
        setUIState,
        lobbyState,
        serverListState,
        setLobbyState,
        quitMultiplayer,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};
export default AppContextProvider;
