import SIGNALS from "../../signals";
import SCREENS from "../../screens";
import { createClient } from "contentful";
import { useContext, useState, useEffect } from "react";
import { documentToReactComponents } from "@contentful/rich-text-react-renderer";
import BackIcon from "../icons/Back";
import { AppContext } from "../../Contexts/AppContext";
import PlaceholderLoading from "react-placeholder-loading";

import "./news.css";
const ue4 = window.ue4;
const config = {
  spaceID: "vimt2d5dik8i",
  deliveryToken: "XZjd8jjyyVnh21UGC4rIpsmimQAbCCZ0NruWY9UQIAU",
};

const client = createClient({
  space: config.spaceID,
  accessToken: config.deliveryToken,
});

export default function NewsFeed() {
  const { navigate, toggleFullscreen } = useContext(AppContext);
  const [newsFeedList, setNewsFeedList] = useState([]);
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    client
      .getEntries({
        content_type: "update",
        order: "sys.createdAt",
      })
      .then((response) => {
        setNewsFeedList(response.items);
        setLoading(false);
      });
  }, []);
  return (
    <div className="news-feed">
      <nav className="header">
        <div className="header-title">
          <h1>KEEB NEWS</h1>
        </div>
        <div
          onClick={() => navigate(SCREENS.NEWS, SCREENS.DESKTOP)}
          className="newspad fs"
        >
          <BackIcon />
        </div>
      </nav>
      <div className="news-feed-content content">
        {loading ? (
          <div className="news-feed-content-item">
            <div className="news-feed-content-item-header">
              <div className="news-feed-content-item-header-title">
                <h1>
                  <PlaceholderLoading shape="rect" width={500} height={80} />
                </h1>
                <span>
                  <PlaceholderLoading shape="rect" width={60} height={40} />
                </span>
              </div>
            </div>
            <div className="news-feed-content-item-content">
              <PlaceholderLoading shape="rect" width={1000} height={20} />
              <PlaceholderLoading shape="rect" width={1000} height={20} />
              <PlaceholderLoading shape="rect" width={1000} height={20} />
              <PlaceholderLoading shape="rect" width={1000} height={20} />
            </div>
          </div>
        ) : (
          newsFeedList.reverse().map((update) => (
            <div className="news-feed-content-item">
              <div className="news-feed-content-item-header">
                <div className="news-feed-content-item-header-title">
                  <h1>{update.fields.title}</h1>
                  <span>
                    {update.sys.createdAt}- keyball v
                    {update.fields.pertainingToKeyballVersionNumber}
                  </span>
                </div>
              </div>
              <div className="news-feed-content-item-content">
                {documentToReactComponents(update.fields.body)}
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
}
