import SIGNALS from "../../signals";
import SCREENS from "../../screens";
import { useContext, useState } from "react";
import BackIcon from "../icons/Back";
import layouts from "../../data/layouts.json";
import { AppContext } from "../../Contexts/AppContext";
import "./Settings.css";

const ue4 = window.ue4;

const layoutModes = {
  NEW: "new",
  UPDATE: "update",
  NONE: "none",
};

export default function Settings() {
  const { navigate, settings, customLayouts } = useContext(AppContext);
  const [layoutMode, setLayoutMode] = useState(layoutModes.NONE);
  const [customName, setCustomName] = useState("");
  const [pleaseConfirmDeletion, setPleaseConfirmDeletion] = useState(false);

  // create all new settings state here, not in ue4

  const selectLayout = (e) => {
    ue4(SIGNALS.SETTINGS__LAYOUT_SELECT, {
      ...settings,
      activeKeyboardLayout: e.target.value,
    });
  };

  const handleCreate = () => {
    setLayoutMode(layoutModes.NEW);
    const newCustomLayoutName = `custom layout ${Math.floor(
      Math.random() * 1000
    )}`;
    const newSettingsState = {
      ...settings,
      activeKeyboardLayout: newCustomLayoutName,
      customLayouts: [
        ...settings.customLayouts,
        {
          name: newCustomLayoutName,
          keys: [
            "1",
            "2",
            "3",
            "4",
            "5",
            "6",
            "7",
            "8",
            "9",
            "0",
            "q",
            "w",
            "e",
            "r",
            "t",
            "y",
            "u",
            "i",
            "o",
            "p",
            "a",
            "s",
            "d",
            "f",
            "g",
            "h",
            "j",
            "k",
            "l",
            ";",
            "z",
            "x",
            "c",
            "v",
            "b",
            "n",
            "m",
            ",",
            ".",
            "/",
          ], // use QWERTY as default
        },
      ],
    };
    console.log("new state after initiating create layout", newSettingsState);
    ue4(SIGNALS.SETTINGS__LAYOUT_CREATE, newSettingsState);
  };

  const handleUpdate = () => {
    setLayoutMode(layoutModes.UPDATE);
    !layouts.includes(customName) &&
      ue4(SIGNALS.SETTINGS__LAYOUT_UPDATE, { name: customName });
  };

  const handleSave = () => {
    setLayoutMode(layoutModes.NONE);
  };

  const handleDelete = () => {
    setLayoutMode(layoutModes.NONE);
    ue4(SIGNALS.SETTINGS__LAYOUT_DELETE, {
      name: settings.activeKeyboardLayout,
    });
  };

  const handleCancel = () => {
    setLayoutMode(layoutModes.NONE);
    ue4(SIGNALS.SETTINGS__LAYOUT_CANCEL);
  };

  const handleRename = (name) => {
    const prevName = settings.activeKeyboardLayout;
    setCustomName(name);
    const newSettingsState = {
      ...settings,
      customLayouts: settings.customLayouts.map((cl) =>
        cl.name === prevName
          ? {
              ...cl,
              name,
            }
          : cl
      ),
    };
    ue4(SIGNALS.SETTINGS__LAYOUT_RENAME, newSettingsState);
  };

  return (
    <div className="settings-container">
      <nav className="header">
        <div className="header-title">
          <h1>SETTINGS</h1>
        </div>
        <div
          onClick={() => navigate(SCREENS.SETTINGS, SCREENS.DESKTOP)}
          className="newspad fs"
        >
          <BackIcon />
        </div>
      </nav>
      <div className="settings-main">
        {settings ? (
          <>
            <div className="settings-content content">
              <h1>Keyboard Layout</h1>
              <div className="options">
                <select
                  defaultValue={settings.activeKeyboardLayout}
                  onChange={selectLayout}
                >
                  {settings.customLayouts.map((l) => (
                    <option value={l.name}>{l.name}</option>
                  ))}
                  {layouts.map((l) => (
                    <option value={l}>{l}</option>
                  ))}
                </select>
              </div>

              <>
                {/* TO DO : fix up the custom layout feature */}
                {/* {layoutMode === layoutModes.NONE && (
                  <button onClick={handleCreate}>Create Custom Layout</button>
                )} */}
                {/* if its a custom layout */}
                {!layouts.includes(settings.activeKeyboardLayout) && (
                  <>
                    {layoutMode === layoutModes.NONE ? (
                      <>
                        <button onClick={handleUpdate}>
                          edit {settings.activeKeyboardLayout}
                        </button>
                        <button onClick={() => setPleaseConfirmDeletion(true)}>
                          delete {settings.activeKeyboardLayout} layout
                        </button>
                        {pleaseConfirmDeletion && (
                          <button
                            className="danger"
                            onClick={() => handleDelete()}
                          >
                            sure?
                          </button>
                        )}
                      </>
                    ) : (
                      <>
                        <h1>Editing</h1>
                        {layouts.includes(customName) &&
                          layoutMode !== layoutModes.NONE && (
                            <span className="warning">
                              Please choose a unique layout name
                            </span>
                          )}
                        <input
                          value={customName}
                          onChange={(e) => handleRename(e.target.value)}
                        />
                        <button onClick={handleSave}>ok</button>
                        <button onClick={handleCancel}>cancel</button>
                      </>
                    )}
                  </>
                )}
              </>
            </div>
          </>
        ) : (
          "loading..."
        )}
      </div>
    </div>
  );
}
