import { useContext } from "react";
import Boot from "./components/Boot/Boot";
import SplashScreen from "./components/SplashScreen/SplashScreen";
import Desktop from "./components/Desktop/Desktop";
import ServerList from "./components/ServerList/ServerList";
import LocalLobby from "./components/LocalLobby/Lobby";
import OnlineLobby from "./components/OnlineLobby/Lobby";
import OnlineOptions from "./components/OnlineOptions/OnlineOptions";
import ModEditor from "./components/ModEditor/ModEditor";
import FileExplorer from "./components/FileExplorer/FileExplorer";
import Tutorial from "./components/Tutorial/Tutorial";
import Settings from "./components/Settings/Settings";
import News from "./components/News/News";
import Quit from "./components/Quit/Quit.js";
import DebugState from "./components/DebugState/DebugState";
import KeyboardDetector from "./components/KeyboardDetector/KeyboardDetector";
import "./styles.css";
import SCREENS from "./screens";
import { AppContext } from "./Contexts/AppContext";

function highQualityRouter(location) {
  switch (location) {
    case SCREENS.BOOT:
      return <Boot />;
    case SCREENS.DEBUG_MODS:
      return <DebugState />;
    case SCREENS.SPLASHSCREEN:
      return <SplashScreen />;
    case SCREENS.DESKTOP:
      return <Desktop />;
    case SCREENS.MOD_EDITOR:
      return <ModEditor />;
    case SCREENS.FILE_EXPLORER:
      return <FileExplorer />;
    case SCREENS.SETTINGS:
      return <Settings />;
    case SCREENS.TUTORIAL:
      return <Tutorial />;
    case SCREENS.SERVER_LIST:
      return <ServerList />;
    case SCREENS.ONLINE_LOBBY:
      return <OnlineLobby />;
    case SCREENS.LOCAL_LOBBY:
      return <LocalLobby />;
    case SCREENS.ONLINE_OPTIONS:
      return <OnlineOptions />;
    case SCREENS.NEWS:
      return <News />;
    case SCREENS.KEYBOARD_DETECTOR:
      return <KeyboardDetector />;
    case SCREENS.INGAME_QUIT_CONFIRMATION:
      return <Quit />;
    default:
      return <Boot />;
  }
}
export default function App() {
  const { UIState, intitialStateReceived, gameMods } = useContext(AppContext);
  return (
    <div className="app">
      {intitialStateReceived ? (
        highQualityRouter(UIState.location)
      ) : (
        <div>{JSON.stringify(gameMods)}</div>
      )}
    </div>
  );
}
