import SIGNALS from "../../signals";
import SCREENS from "../../screens";
import { AppContext } from "../../Contexts/AppContext";
import { useContext, useEffect } from "react";
import "./ModEditor.css";
import BackIcon from "../icons/Back";
import JSONIcon from "../icons/JSON";
import { useState } from "react";
import FormEditor from "../FormEditor/FormEditor.js";
import { JSONEditor } from "react-json-editor-viewer";
const ue4 = window.ue4;

const FileDropDown = () => {
  const { navigate } = useContext(AppContext);

  return (
    <div className="file-dropdown">
      <div className="file-dropdown-menu">
        <div className="file-dropdown-menu-item">
          <div className="file-dropdown-menu-item-text">
            <span>New</span>
          </div>
        </div>
        <div
          className="file-dropdown-menu-item"
          onClick={() => navigate(SCREENS.MOD_EDITOR, SCREENS.FILE_EXPLORER)}
        >
          <div className="file-dropdown-menu-item-text">
            <span>Open</span>
          </div>
        </div>
        <div className="file-dropdown-menu-item">
          <div className="file-dropdown-menu-item-text">
            <span>Copy</span>
          </div>
        </div>
        <div className="file-dropdown-menu-item">
          <div className="file-dropdown-menu-item-text">
            <span>Export</span>
          </div>
        </div>
        <div className="file-dropdown-menu-item">
          <div className="file-dropdown-menu-item-text">
            <span>Import</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default function ModEditor() {
  const { navigate, settings, gameMods, updateMod, UIState } =
    useContext(AppContext);
  const [dropdownOpen, setDropdownOpen] = useState(false);
  const [editorViewMode, setEditorViewMode] = useState("form");
  const [activeMod, setActiveMod] = useState(null);

  const {
    lastSelectedFileInUI,
    setLastSelectedFileInUI,
    lastSelectedFileInUIType,
  } = UIState;

  useEffect(() => {
    setActiveMod(() => {
      return gameMods.find((mod) => {
        return mod.meta.id === lastSelectedFileInUI;
      });
    });
  }, [settings, gameMods, UIState.lastSelectedFileInUI]);

  const handleDropdownClick = () => {
    ue4(!dropdownOpen ? SIGNALS.SOUND__CLICK_ON : SIGNALS.SOUND__CLICK_OFF);
    setDropdownOpen(!dropdownOpen);
  };
  return (
    <div id="mod-editor">
      <nav>
        <div id="mod-editor-tabs">
          <div onClick={handleDropdownClick} className="navpad mods">
            <span>file</span>
          </div>
          <div
            onClick={() =>
              setEditorViewMode((p) => (p === "form" ? "JSON" : "form"))
            }
            className="navpad mods"
          >
            <JSONIcon mode={editorViewMode} />
          </div>
          <div
            onClick={() => navigate(SCREENS.MOD_EDITOR, SCREENS.DESKTOP)}
            className="navpad fs"
          >
            <BackIcon />
          </div>
        </div>
      </nav>
      <div className="content">
        {dropdownOpen && <FileDropDown />}
        {/* {jumpToOpen && (
          <CategoriesDropDown callback={() => setJumpToOpen(false)} />
        )} */}
        {editorViewMode === "form" ? (
          <FormEditor
            settings={settings}
            gameMods={gameMods}
            updateMod={updateMod}
            lastSelectedFileInUI={lastSelectedFileInUI}
            setLastSelectedFileInUI={setLastSelectedFileInUI}
            lastSelectedFileInUIType={lastSelectedFileInUIType}
            activeMod={activeMod}
          />
        ) : activeMod ? (
          <div
            className="json-editor"
            // contentEditable={true}
            // spellCheck={false}
          >
            <JSONEditor
              data={activeMod}
              collapsible={false}
              onChange={console.log}
              view="dual"
            />
          </div>
        ) : (
          ""
        )}
      </div>
    </div>
  );
}
